<script>
import Vue from "vue";
import SortedTablePlugin from "vue-sorted-table";
import functions from "../../../util/functions.js";
import constants from "../../../util/constants";

Vue.use(SortedTablePlugin, {
  ascIcon: '<i class="mdi mdi-arrow-down"></i>',
  descIcon: '<i class="mdi mdi-arrow-up"></i>',
});

export default {
  data: function () {
    return {
      constants,

      is_doing_action: false,
      action_message: "En cours",

      sortBy: "Nom & prénom",
      perPage: 10,
      currentPage: 1,
      sortDesc: false,
      sortByFormatted: true,
      filterByFormatted: true,
      sortable: true,
      is_loading_categories_faqs: true,
      fields: [
        { key: "n", sortable: false },
        { key: "nom", sortable: true },
        { key: "nbr_de_questions", sortable: true },
        { key: "action", sortable: true },
      ],
      all_categories_faqs: [],
      list_categories_categories_faqs: [],
      search_text: "",
    };
  },
  async created() {
    this.is_loading_categories_faqs = true;
    let url = this.constants.api_url + "/categories_faqs/get.php";
    let response = await fetch(url, {
      method: "get",
      cache: "no-cache",
      credentials: "include",
      headers: {
        Authorization: sessionStorage.getItem("token"),
      },
    });
    let data = await response.json();
    if (data.status == "SUCCESS") {
      this.all_categories_faqs = data.categories_faqs.map((categorie_faq) => {
        return {
          ...categorie_faq,
          n:
            data.categories_faqs.findIndex(
              (f) => f.id_categorie_faq == categorie_faq.id_categorie_faq
            ) + 1,
          nom: categorie_faq.libelle,
          nbr_de_questions: categorie_faq.faqs.length,
          action: categorie_faq.id_categorie_faq,
        };
      });
    } else {
      this.$swal({
        type: "error",
        title: "Une erreur est survenue",
        text: "Status : " + data.status + "\nMessage : " + data.message,
        //footer: '<a href>Why do I have this issue?</a>'
      });
    }
    this.is_loading_categories_faqs = false;
  },
  computed: {
    rows() {
      return this.all_categories_faqs.length;
    },
    categories_faqs() {
      return this.all_categories_faqs.filter((categorie_faq) => {
        if (this.search_text == "") {
          return this.all_categories_faqs;
        }
        let attrs = ["libelle"];
        for (let attr of attrs) {
          if (
            categorie_faq[attr] != undefined &&
            categorie_faq[attr]
              .toLowerCase()
              .toLowerCase()
              .includes(this.search_text.toLowerCase())
          ) {
            return true;
          }
        }
      });
    },
  },
  methods: {
    ...functions,
    async doDeleteFaq(id_categorie_faq) {
      let res = await this.$swal({
        title: "Supprimer ?",
        text: "Voulez-vous vraiment supprimer ce groupe ?",
        type: "question",
        showCancelButton: true,
        confirmButtonColor: "#d33",
        cancelButtonColor: "#3085d6",
        confirmButtonText: "Supprimer",
        cancelButtonText: "Annuler",
      });
      if (!res.value) {
        return;
      }
      this.action_message = "Suppression  en cours";
      this.is_doing_action = true;
      let url =
        this.constants.api_url +
        "/categories_faqs/delete.php?id_categorie_faq=" +
        id_categorie_faq;
      let response = null;
      try {
        response = await fetch(url, {
          method: "get",
          cache: "no-cache",
          body: JSON.stringify(this.categorie_faq),
          credentials: "include",
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS") {
        this.all_categories_faqs = this.all_categories_faqs.filter(
          (categorie_faq) => categorie_faq.id_categorie_faq != id_categorie_faq
        );
        this.$swal({
          type: "success",
          title: "Effectué",
          text: "La groupe a été supprimée avec succès. ",
          //footer: '<a href>Why do I have this issue?</a>'
        });
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
  },
};
</script>

<template>
  <section class="mt-5">
    <div class="page-header">
      <h3 class="page-title">Liste des Groupes</h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <a href="javascript:void(0);">Administration</a>
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            Liste des Groupes
          </li>
        </ol>
      </nav>
    </div>
    <div v-if="!is_loading_categories_faqs">
      <div v-if="is_doing_action">
        <div class="card mb-4">
          <div class="card-body">
            <div class="circle-loader"></div>
            <p class="text-center mt-4">{{ action_message }}</p>
          </div>
        </div>
      </div>
      <div class=" ">
        <div class="card">
          <div class="card-body">
            <!-- <h4 class="card-title mb-5">Liste de Faqs</h4> -->
            <div class="row d-flex justify-content-end">
              <div class="col-md-7">
                <button
                  type="button"
                  @click="$router.push('/admin/faqs/categories-faqs/nouveau')"
                  :disabled="is_doing_action"
                  class="btn-icon-text btn btn-primary mb-3 mb-md-0"
                >
                  <i class="fa fa-plus btn-icon-prepend"></i>
                  Nouveau Groupe
                </button>
              </div>
              <div class="col-md-5">
                <b-form-group label="" label-for="input5">
                  <b-form-input
                    type="text"
                    v-model="search_text"
                    placeholder="Rechercher"
                  ></b-form-input>
                </b-form-group>
              </div>
            </div>

            <div class="table-responsive">
              <b-table
                :items="categories_faqs"
                id="table-list"
                responsive
                :per-page="perPage"
                :current-page="currentPage"
                :fields="fields"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
              >
                <template v-slot:cell(status)="data">
                  <span v-html="data.value"></span>
                </template>
                <template v-slot:cell(action)="data">
                  <div class="d-flex justify-content-center">
                    <button
                      class="btn btn-sm btn-outline-primary py-1 mr-1"
                      title="Modifier ce categorie_faq"
                      @click="
                        () =>
                          $router.push(
                            '/admin/faqs/categories-faqs/editer/' + data.value
                          )
                      "
                      :disabled="is_doing_action"
                    >
                      <i class="fa fa-pencil"></i>
                    </button>
                    <button
                      @click="doDeleteFaq(data.value)"
                      class="btn btn-sm btn-outline-danger py-1"
                      title="Supprimer ce categorie_faq"
                      :disabled="is_doing_action"
                    >
                      <i class="fa fa-trash"></i>
                    </button>
                  </div>
                </template>
                <template v-slot:cell(profil)="data">
                  <span v-html="data.value"></span>
                </template>
              </b-table>
              <b-pagination
                v-model="currentPage"
                :total-rows="rows"
                :per-page="perPage"
                aria-controls="table-list"
                align="right"
              >
              </b-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="mt-5 pt-4" v-else>
      <div class="card">
        <div class="card-body">
          <div class="circle-loader"></div>
          <p class="text-center mt-4">Chargement des données en cours</p>
        </div>
      </div>
    </div>
    <div></div>
  </section>
</template>
